const galleryList = document.querySelector('.gallery');

const imageElements = images.map((image) => {

  const listItem = document.createElement('li');
    listItem.classList.add('gallery-item');  
    
    const conItem = document.createElement('div');
    conItem.classList.add('cont-item'); 

  const linkIt = document.createElement('a');
  linkIt.classList.add('gallery-link');  
  linkIt.setAttribute("href", image.original);  

  const imgEl = document.createElement('img');
  imgEl.classList.add('gallery-image');  
  imgEl.setAttribute("src", image.preview);
  imgEl.setAttribute("alt", image.description);

  linkIt.append(imgEl);
  conItem.append(linkIt);
  listItem.append(conItem);

  return listItem;
});

galleryList.append(...imageElements);


const lightbox = new SimpleLightbox('.gallery a', {
    captionDelay: 250,  
    captionData: 'alt',
    nav: true,
});
